const getters = {

}
const mutations = {
  updateLabels(state, payload) {
    state.labels = payload
  },
  updateMainChart(state, payload) {
    state.mainChart = payload
  },
  updateMapChart(state, payload) {
    state.mapChart = payload
  },
  changeLabelStatus(state, payload) {

  },
  updatePrograms(state, payload) {
    state.programs = payload
  },
  saveDate(state, newValue) {
    state.date = newValue;
  },
  saveDate2(state, newValue) {
    state.date2 = newValue;
  },
  addPreset(state, newPreset) {
    state.presets.items.push(newPreset)
  },
  updateNewPresetDialog(state, payload) {
    state.newPresetDialog = payload;
  },
  updateAllStatistics(state, payload) {
    state.allStatistics = payload;
  },
  updateLabels(state, payload) {
    state.labels = payload;
  },
  updateServerData(state, payload) {
    state.serverData = payload;
  },
  updateDeletedCount(state, payload){
    state.deletedCount = payload;
  }
}
const actions = {
  toggleSeries(context, payload) {
    debugger
    let collection = context.state[payload.collectionName]
    let item = collection.items.find(item => item.name == payload.itemName);
    item.isActive = !item.isActive
    let bool = collection.items.some(item => item.isActive == false && item.type == payload.type)
    collection[payload.type].subheader.isActive = !bool;
    context.state.mainChart.toggleSeries(payload.itemName)
  },
  hideSeries(context, payload) {
    context.state.labels.items[payload].isActive = !context.state.labels.items[payload].isActive;
    let label = context.state.labels.items[payload].name
    context.state.mainChart.toggleSeries(label)
  },
  showSeries(context, payload) {
    context.state.labels.items[payload].isActive = !context.state.labels.items[payload].isActive;
    let label = context.state.labels.items[payload].name
    context.state.mainChart.toggleSeries(label)
  },
  toggleAllSeries(context, payload) {
    debugger
    context.state.labels[payload].subheader.isActive = !context.state.labels[payload].subheader.isActive;
    if (context.state.labels[payload].subheader.isActive) {
      context.state.labels.items.forEach((item, index, array) => {
        if (item.type == payload)
          item.isActive || context.dispatch('showSeries', index)
      });
    } else {
      context.state.labels.items.forEach((item, index, array) => {
        debugger
        if (item.type == payload)
          item.isActive && context.dispatch('hideSeries', index)
      });
    }
  },
  toggleProgram(context, payload) {
    debugger
    let collection = context.state[payload.collectionName]
    let program = collection.items[payload.index]
    let deletedCount = context.state.deletedCount;
    let start = program.start / 5;
    let count = program.end / 5 - start;
    start -= deletedCount
    let data = context.state.serverData;
    let arrKeys = Object.keys(data);
    arrKeys = arrKeys.slice(0, arrKeys.length - 1);

    if (program.isActive) {
      arrKeys.forEach(element => {
        data[element].splice(start, count)
      });
      deletedCount +=count;
    }else{
      deletedCount -= count;
    }
    context.commit('updateDeletedCount',deletedCount)
    context.dispatch("updateSeriesOfChart", data);
    program.isActive = !program.isActive
    let bool = collection.items.some(item => item.isActive == false)
    collection.subheader.isActive = !bool;
  },
  toggleAllPrograms(context, payload) {
    debugger
    context.state.programs.subheader.isActive = !context.state.programs.subheader.isActive;
    if (context.state.programs.subheader.isActive) {
      context.state.programs.items.forEach((item, index, array) => {
        item.isActive || (item.isActive = !item.isActive)
      });
    } else {
      context.state.programs.items.forEach((item, index, array) => {
        item.isActive && (item.isActive = !item.isActive)
      });
    }
  },
  updateChart(context, payload) {
    let data = payload;
    context.commit('updateServerData', data);
    let arrKeys = Object.keys(data);
    arrKeys = arrKeys.slice(0, arrKeys.length - 1);
    let allSeries = [];

    arrKeys.forEach(element => {
      allSeries = allSeries.concat(data[element]);
    });
    context.commit.all
    let series = [
      {
        name: " ",
        data: allSeries.map(elem => [
          elem[0],
          elem.slice(1).reduce((a, b) => Math.round(a + b))
        ])
      }
    ];
    data.channels.forEach((item, index, array) => {
      series.push({
        name: item,
        data: allSeries.map(elem => [
          elem[0],
          Math.round(Number(elem[index + 1]))
        ])
      });
    });
    context.dispatch('createLabels', series);
    debugger
    context.state.mainChart.updateSeries(series);
    context.state.mapChart.updateSeries(series);
  },
  updateSeriesOfChart(context, payload) {
    debugger
    let data = payload;
    let arrKeys = Object.keys(data);
    arrKeys = arrKeys.slice(0, arrKeys.length - 1);
    let allSeries = [];

    arrKeys.forEach(element => {
      allSeries = allSeries.concat(data[element]);
    });
    let series = [
      {
        name: " ",
        data: allSeries.map(elem => [
          elem[0],
          elem.slice(1).reduce((a, b) => Math.round(a + b))
        ])
      }
    ];
    data.channels.forEach((item, index, array) => {
      series.push({
        name: item,
        data: allSeries.map(elem => [
          elem[0],
          Math.round(Number(elem[index + 1]))
        ])
      });
    });
    context.state.mainChart.updateSeries(series);
    context.state.mapChart.updateSeries(series);
  },
  createLabels(context, payload) {
    let series = {
      collectionName: "labels",
      channel: {
        subheader: {
          isActive: true,
          name: "",
        },
      },
      author: {
        subheader: {
          isActive: true,
          name: "",
        },
      },
      actions: {
        clickedOne: "messages_tab/toggleSeries",
        clickedAll: "messages_tab/toggleAllSeries"
      },
      items: [
        {
          isActive: true,
          displayName: " ",
          name: " ",
          color: "#000051",
          type: "author"
        },
        {
          isActive: true,
          displayName: " ",
          name: " ",
          color: "#f4511e",
          type: "author"
        },
        {
          isActive: true,
          displayName: " 1-5 ",
          name: " 1-5 ",
          color: "#e91e63",
          type: "author"
        },
        {
          isActive: true,
          displayName: "  5 ",
          name: "  5 ",
          color: "#4b2c20",
          type: "author"
        }
      ]
    }

    payload.forEach((item, index, array) => {
      series.items.push({
        isActive: true,
        displayName: item.name,
        name: item.name,
        color: context.state.colors[index],
        type: "channel"
      })
    })
    context.commit('updateLabels', series);
  },
  toggleLabels(context, payload) {
    context.state.mainChart.updateOptions({
      dataLabels: {
        enabled: payload
      }
    })
  },
  togglePreset(context, payload) {
    let collection = context.state.labels
    let preset = context.state.presets.items.find(el => el.displayName == payload)
    let seriesToShow = context.state.labels.items.filter(el => preset.list.indexOf(el.name) != -1)
    let seriesToHide = context.state.labels.items.filter(el => preset.list.indexOf(el.name) == -1);
    seriesToShow.forEach(function (item, index, array) {
      if (!item.isActive) {
        context.state.mainChart.toggleSeries(item.name)
        item.isActive = !item.isActive
        let bool = collection.items.some(item => item.isActive == false)
        collection[item.type].subheader.isActive = !bool;
      }
    });
    seriesToHide.forEach(function (item, index, array) {
      if (item.isActive) {
        context.state.mainChart.toggleSeries(item.name)
        item.isActive = !item.isActive
        let bool = collection.items.some(item => item.isActive == false)
        collection[item.type].subheader.isActive = !bool;
      }
    })
  },
  savePreset(context, payload) {
    let newPreset = {
      isActive: true,
      displayName: payload.name,
      name: payload.name,
      list: payload.labels,
    }
    context.commit('addPreset', newPreset)
  },
  getStatistics(context, payload) {
    debugger
    let dateFrom = context.state.date;
    let dateTo = context.state.date2;

    axios.get(
      `http://dev533.iactive.pro/scr/statisctic.php?userName=79671035533&dateFrom=${dateFrom}&dateTo=${dateTo}`
    ).then(response => {
      debugger
      context.dispatch('updateChart', response.data)
    });
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}